"use client";
import { useEffect, useState } from "react";
import "./globals.css";
import ThemeToggle from "@/components/ThemeToggle";

export default function RootLayout({ children }) {
  const [theme, setTheme] = useState("light");

  // Load saved theme on first load
  useEffect(() => {
    const saved = localStorage.getItem("theme");
    if (saved) {
      setTheme(saved);
      document.documentElement.classList.toggle("dark", saved === "dark");
    }
  }, []);

  // Toggle theme
  const toggleTheme = () => {
    const newTheme = theme === "light" ? "dark" : "light";
    setTheme(newTheme);

    // Save preference
    localStorage.setItem("theme", newTheme);

    // Apply to HTML tag
    document.documentElement.classList.toggle("dark", newTheme === "dark");
  };

  return (
    <html lang="en">
      <body className="bg-gray-100 dark:bg-gray-900 dark:text-white min-h-screen">

        {/* Top Navbar */}
        <header className="p-4 bg-white dark:bg-gray-800 shadow flex justify-between">
          <h1 className="text-xl font-bold">Matrimonial Admin Panel</h1>

          {/* Theme Toggle Button */}
          <ThemeToggle theme={theme} toggleTheme={toggleTheme} />
        </header>

        {/* Main Content */}
        <main className="p-6">{children}</main>
      </body>
    </html>
  );
}
